.mainNavigation__HamburgerIcon {
  display: none;
  position: absolute;
  left: 20px;
}

.mainNavigation {
  display: flex;
}
.mainNavigation .mainNavigation__img {
  max-width: 200px;
  object-fit: contain;
  height: max-content;
  margin-left: 15px;
}
.mainNavigation .mainNavigation__list {
  display: flex;
  list-style-type: none;
  justify-content: center;
  width: 100%;
  align-items: center;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper {
  margin: 0 2vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: max-content;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper .mainNavigation__link {
  text-decoration: none;
  padding: 0.3rem 1rem;
  white-space: nowrap;
  color: var(--mainNavigation-link);
  transition: 800ms color ease;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper .mainNavigation__link:visited {
  color: var(--mainNavigation-link);
  transition: 800ms color ease;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper .mainNavigation__link:hover {
  transition: 800ms color ease;
  color: #4fb2f0;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper .mainNavigation__link__animatedBar {
  height: 3px;
  width: 0;
}
.mainNavigation .mainNavigation__list .mainNavigation__link__wrapper .mainNavigation__link__animatedBar:hover {
  width: 100%;
  transition: 300ms width linear;
  background-color: var(--mainNavigation-link);
}

.m-0 {
  margin: 0;
}

.p-0 {
  padding: 0;
}

